class BoxInputEnumLarge extends BoxInputEnum {

	protected _createPopup(): IPopupable & IActionableList<IBoxEnumEltWedlet> {
		const popup = new PopupEnumTree().initialize({
			reg: REG.findReg(this),
			actionContext: this,
			resizer: {}
		});
		popup.onNextClose().then((key: string) => {
			if (key != null && !this.isSelected(key)) WEDLET.replaceValue(this.wedlet, this.key, key);
		});
		return popup;
	}

	async onClick(this: BoxInputEnum, ev: MouseEvent) {
		const popup = this.popup;
		if (popup && !popup.contains(ev.target as Node)) {
			if (!popup.actions) popup.actions = await this.provider.getActions(this);
			popup.show({
				viewPortY: "middle",
				fixSize: true,
				initWidth: "60em"
			}, this);
		}
	}
}

window.customElements.define("box-input-enumlarge", BoxInputEnumLarge);


export interface PopupEnumTree {
	initialize(init?: OPopupEnumTreeInit): this;
}

export interface OPopupEnumTreeInit extends OPopupMenuInit {
	actionContext: BoxInputEnumLarge;
}

export class PopupEnumTree extends MxPopupable(BaseElement, EPopupType.menu) implements IGridLineDrawer, IPopupable<string>, IActionableList<BoxInputEnumLarge> {

	actions: IAction<IBoxEnumEltWedlet>[];

	_actionContext: BoxInputEnumLarge;
	get actionContext(): BoxInputEnumLarge {return this._actionContext};

	dataHolder: GridDataHolderJsonTree<IAction<IBoxEnumEltWedlet>>;

	grid: Grid;
	search: HTMLInputElement;

	protected _initialize(init: OPopupEnumTreeInit) {
		super._initialize(init);
		if ('restoreFocus' in init) this.restoreFocus = init.restoreFocus;
		this._actionContext = init.actionContext;
		this.dataHolder = new DataHolderActions(this.actionContext);
		REG.reg.installSkin("box-input-enumlarge");

		const colDefs = [new GridColTreeDef('tree')
			.setFlex('10rem', 1, 1).setMinWidth('55px')
			.setCellBuilder(new CellBuilderAction(this.actionContext))];

		const searchLabel = this.appendChild(<label id="search"/>)
		this.search = searchLabel.appendChild(<input type="search" spellcheck="false" placeholder="Filtrer..." oninput={this.onSearchInput} onkeydown={this.onKeyPress}/>) as HTMLInputElement;
		this.grid = this.appendChild(new Grid().initialize({
			selType: "monoOver",
			columnDefs: colDefs,
			dataHolder: this.dataHolder,
			lineDrawer: this,
			hideHeaders: true,
			skinScroll: "scroll/small",
			skinOver: "box-input-enumlarge-popup/grid"
		}));

		this.grid.uiEvents.on('rowClick', (row: GridDataRowJson<IAction<IBoxEnumEltWedlet>> | null, ev: MouseEvent) => {
			if (row?.rowDatas.isToggle(this.actionContext) && (ev.target as Element).localName === "span") {
				this.close(row.rowDatas.getId());
			}
		});
		this.grid.addEventListener('keypress', function (this: Grid, ev: KeyboardEvent) {
			const me = this.parentElement as PopupEnumTree;
			if (ev.key === ' ' || ev.key === 'Enter') {
				const row = this.dataHolder.getRow(this.getSelectedRow()) as GridDataRowJson<IAction<IBoxEnumEltWedlet>>;
				if (row?.rowDatas.isToggle(me.actionContext)) me.close(row.rowDatas.getId());
				ev.preventDefault();
			} else me.search.focus();
		});
	}

	protected onSearchInput(this: HTMLInputElement, ev: InputEvent) {
		(this.parentElement.parentElement as PopupEnumTree).redrawDatas();
	}

	protected onKeyPress(this: HTMLInputElement, ev: KeyboardEvent) {
		if (ev.key === 'ArrowDown') {
			const tree = this.parentElement as PopupEnumTree;
			if (tree.grid.dataHolder.countRows() > 0) {
				if (tree.grid.getSelectedRow() === undefined) tree.grid.setSelectedRows(0);
				tree.grid.focus();
			}
		}
	}

	/** */
	redrawLine(row: GridDataRowJson<IAction<IBoxEnumEltWedlet>>, line: HTMLElement): void {
		const d = row.rowDatas;
		DOM.setAttrBool(line, "data-user-disabled", !d.isEnabled(this._actionContext));
		DOM.setAttr(line, "selected", d.isToggle(this._actionContext) ? d.getDatas("toggle", this._actionContext) as any as 'true' | 'false' | 'null' : "null");
	}

	show(anchor?: OPopupAnchor, uiContainer?: Node, logicalParent?: Element): boolean {
		this.redrawDatas();
		return super.show(anchor, uiContainer, logicalParent);
	}

	protected async redrawDatas() {
		const filter = this.search.value;
		if (filter) {
			this.dataHolder.setDefaultOpenState(GridDataHolderJsonTree.defaultOpened as any);
			this.dataHolder.setDatas(await this.actionContext.provider.getActions(this.actionContext, filter));
		} else {
			this.dataHolder.setDefaultOpenState(GridDataHolderJsonTree.defaultClosed as any);
			this.dataHolder.setDatas(this.actions);
		}
	}
}

