class DataHolderActions extends MxGridTreeUntouchedDatas(GridDataHolderJsonTree)<IAction<IBoxEnumEltWedlet>> {
	actionContext: IBoxEnumEltWedlet;

	constructor(actionContext: IBoxEnumEltWedlet) {
		super();
		this.actionContext = actionContext;
	}

	ch(parent: IAction<IBoxEnumEltWedlet>): IAction<IBoxEnumEltWedlet>[] | undefined | null {
		if (!parent.isMenu(this.actionContext)) return undefined;
		return (parent.getDatas("menu", this.actionContext) || []) as IAction<IBoxEnumEltWedlet>[];
	}

	setCh(parent: IAction<IBoxEnumEltWedlet>, ch: IAction<IBoxEnumEltWedlet>[] | null): IAction<IBoxEnumEltWedlet>[] | null {
		(parent as ActionMenu<IBoxEnumEltWedlet>).setActions(ch);
		return ch;
	}
}

class CellBuilderAction<C> extends CellBuilderLabel /*CellBuilderIconLabel*/ {
	ctx: C;

	constructor(actionContext: C) {
		super('');
		this.ctx = actionContext;
	}

	redrawCell(row: GridDataRowJson<IAction<C>>, root: HTMLElement) {
		if (row.rowDatas instanceof ActionSeparator) {
			root.parentElement.classList.toggle("sep", true);
			DOM.setTextContent(root, null);
			DOM.setAttr(root, "title", null);
		} else {
			root.parentElement.classList.toggle("sep", false);
			super.redrawCell(row, root);
		}
	}

	protected _getValue(row: GridDataRowJson<IAction<C>>): any {
		return row.rowDatas.getLabel(this.ctx);
	}

	// protected _getIcon(row: GridDataRowJson<IAction<C>>): string {
	// 	return row.rowDatas.getIcon(this.ctx);
	// }

	protected _getDescription(row: GridDataRowJson<IAction<C>>): any {
		return row.rowDatas.getDescription(this.ctx);
	}

}